<template>
  <van-empty :description="description" :image="emptyImage" class="empty" />
</template>

<script>
import { Empty } from "vant";
import emptyImage from "./images/empty.svg";

export default {
  name: "viEmpty",
  components: { [Empty.name]: Empty },
  props: {
    description: {
      type: String,
      default: "空空如也"
    }
  },
  data() {
    return {
      emptyImage,
    };
  },
};
</script>

<style lang="less" scoped>
.empty {
	width: 200px;
	height: 200px;
	position: absolute;
	left: 50%;
	top: 50%;
	transform: translate(-50%, -50%);
  :deep(.van-empty__description) {
    padding: 0;
    text-align: center;
    font-size: 18px;
  }
}
</style>
